@keyframes nestingLinkShow {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes nestingLinkClose {
  0% {
    transform: translateY(0%);
    opacity: 1;
  }
  20% {
    opacity: 0;
  }
  100% {
    transform: translateY(-100%);
    opacity: 0;
  }
}

@keyframes nestingListOpenDesk {
  0% {
    transform: translateY(-50%);
    height: 0;
  }
  100% {
    transform: translateY(0%);
    height: 100%;
  }
}

@keyframes nestingListOpenMobile {
  0% {
    transform: translateY(-20%);
    height: 0;
  }
  100% {
    transform: translateY(0%);
    height: 100%;
  }
}

@keyframes nestingListCloseMobile {
  0% {
    transform: translateY(0%);
    height: 100%;
    opacity: 0.5;
  }
  100% {
    transform: translateY(-20%);
    height: 0;
    opacity: 0;
  }
}

@keyframes mainNavOpen {
  0% {
    transform: translateY(-87%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes mainNavClose {
  0% {
    transform: translateY(0%);
  }
  100% {
    transform: translateY(-86%);
  }
}

.nesting-list-animation-desk {
